export default "@import 'taiga-ui-local';\n\n@color-mobile-gray: #9299a2;\n\n:host {\n    font-family: -apple-system, BlinkMacSystemFont, Roboto, sans-serif;\n}\n\n.portal {\n    .center-left();\n    position: fixed;\n    bottom: 1.25rem;\n    animation: tuiFadeIn var(--tui-duration) var(--tui-duration);\n    animation-fill-mode: backwards;\n}\n\n.header {\n    position: sticky;\n    top: 0;\n    width: calc(100% + 4rem);\n    margin-top: -2rem;\n    margin-left: -2rem;\n    text-align: center;\n    word-wrap: break-word;\n    background: var(--tui-support-20);\n    border-radius: 1rem 1rem 0 0;\n    transform-origin: top left;\n}\n\n.background {\n    position: sticky;\n    top: -1rem;\n    height: 2rem;\n    width: calc(100% + 4rem);\n    margin: -2rem 0 -2rem -2rem;\n    background: var(--tui-support-20);\n    border-radius: 1rem 1rem 0 0;\n}\n\n.wrapper {\n    background: var(--tui-base-02);\n    padding: 3.125rem 0 2rem;\n}\n\n.logo {\n    position: relative;\n    height: 5.5rem;\n    color: var(--tui-base-01);\n}\n\n.date {\n    display: block;\n    font-size: 1.0625rem;\n    line-height: 1.125rem;\n    padding: 0.75rem 0;\n}\n\n.icon[data-size='m'] {\n    position: relative;\n    margin-top: 0.25rem;\n    width: 5rem;\n    height: 5rem;\n    box-shadow: 0 0 0 2px var(--tui-base-02);\n    color: var(--tui-base-02);\n    background: var(--tui-support-20);\n}\n\n.description {\n    font-size: 1.0625rem;\n    line-height: 1.125rem;\n    margin: 0.5rem 0 0;\n    color: @color-mobile-gray;\n}\n\n.title {\n    font-weight: bold;\n    font-size: 1.0625rem;\n    line-height: 1.25rem;\n    margin: 1.5rem 0 0.25rem;\n}\n\n.money {\n    font-size: 2.125rem;\n    line-height: 2.5rem;\n}\n";